.contactInfo-paging {
    border-bottom: 1px solid var(--color-border-light);
    position: relative;
}

.contactInfo-paging-container {
    border: 1px solid var(--color-border-light);
    border-bottom: 2px solid white;
    position: absolute;
    bottom: -2px;
    padding: 0.5rem 1.5rem;
}

.contactInfo-container{
    display: flex;
    justify-content: center;
    gap: 3rem;
}

.contactInfo-contactInfo_list{
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.contactInfo-contactInfo_list li{
    display: flex;
    gap: 1rem;
}

.YandexMap{
    width: 902px;
    height: 509px;
}

.contactInfo__contactForm {
    text-align: center;
    padding: 2rem;
    margin-bottom: 6rem;
    flex-direction: column;
}

.contactInfo__contactForm  h2{
   padding-bottom: 1rem;
}

.contactInfo__contactForm  button{
    margin: 1rem auto;
}

.contactForm{
    padding: 4rem;
}

.contactForm-container{
    display: flex;
    justify-content: center;
    gap: 4rem;
}

.contactForm-Input-container{
    display: flex;
    flex-direction: column;
    padding-bottom: 1rem;
}

.contactForm-Input-container input{
    padding: 0.3rem;
    border: none;
    border-bottom: 1px solid var(--color-h);
    text-transform: capitalize;
    outline: none;
    min-width: 16rem;
}



@media screen and (max-width: 1200px) {
    .contactInfo-container{
        flex-direction: column;
    }
    
    .YandexMap{
        width: 100%;
        height: 50vh;
    }

    .contactForm-container{
        flex-direction: column;
        gap: 1rem;
    }

    .contactForm{
        padding: 1rem 0;
    }

    .contactForm-Input-container input{
        min-width: 4rem;
    }
    
  
  }